import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { FiAlertCircle } from "react-icons/fi";
import { LuX } from "react-icons/lu";

const Login = () => {
  const [idFocused,setIdfocused]=useState(false);
  const [pwFocused,setPwfocused]=useState(false);
  const [idError,setIdError]=useState(false);
  const [pwError,setPwError]=useState(false);
  const [isCode,setIsCode]=useState(false);
  const navigate=useNavigate();
  const handleSubmit=(e)=>{
    e.preventDefault();
  }
  return (
    <div className="login">
      <div className="login-popup">
        <p className="close" onClick={()=>{navigate('/')}}><LuX /></p>
        <h2></h2>
        <form onSubmit={handleSubmit}>
          <label className={`${idFocused? "focused":''} ${idError? "error":''}`}>
            <span>    </span>
            <input
              type="text"
              onFocus={()=>{setIdfocused(true); setIdError(false);}}
              onBlur={(e)=>{
                if(e.target.value===''){
                  setIdfocused(false);
                  setIdError(true);
                }
              }}
            />
          </label>
          {idError &&<p className="input-error"><FiAlertCircle /><p>    .</p></p>}
          {
            !isCode && (<label className={`${pwFocused? "focused":''} ${pwError? "error":''}`}>
              <span></span>
              <input
                type="password"
                onFocus={()=>{setPwfocused(true); setPwError(false);}}
                onBlur={(e)=>{
                  if(e.target.value===''){
                    setPwfocused(false);
                    setPwError(true);
                  }
                }}
              />
            </label>)
          }
          {pwError &&<p className="input-error"><FiAlertCircle /><p> 4~60  .</p></p>}
          {isCode && <p className="warningTxt">      </p>}
          <button type="submit">
          {isCode ? "  ":""}
          </button>
          <p></p>
          <button onClick={()=>{
            setIsCode(!isCode);
            setIdError(false);
            setPwError(false);
            }}>
            {isCode ? " ":"  "}
          </button>
          <a href="#">
            {isCode? "   ?":" ?"}
          </a>
        </form>
        <div className="login-box">
          <label>
            <input type="checkbox"/>
            <span>  </span>
          </label>
          <p>Netflix  ?
            <a href="#">  .</a>
          </p>
          <p>  Google reCAPTCHA      .
            <a href="#">  .</a>
          </p>
        </div>
      </div>
    </div>
  );
};

export default Login;